<template>
  <div>
    <a-modal v-model="dialog_addMeeting" centered title="จองห้องประชุม" @ok="handleOk">
      <a-form :form="form" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
        <a-form-item label="โปรดเลือกห้องประชุมด้านล่างนี้">
          <!-- <a-card> -->
          <v-row class="">
            <v-col cols="4" v-for="items in array_data" :key="items">
              <a-button
                class=""
                :disabled="room_value"
                :size="size"
                @click="select_room(items)"
              >
                Room : {{ items }}
              </a-button>
            </v-col>
          </v-row>
          <!-- </a-card> -->
        </a-form-item>

        <a-form-item label="หัวข้อการประชุม:">
          <a-input
            v-decorator="[
              'agenda',
              {
                rules: [{ required: true, message: 'โปรดระบุหัวข้อการประชุม' }],
              },
            ]"
            placeholder="โปรดระบุหัวข้อการประชุม"
          />
        </a-form-item>

        <a-form-item label="เชิญผู้ร่วมประชุม:">
          <a-input-search
            placeholder="ค้นหาเพื่อเชิญผู้ร่วมประชุม"
            enter-button
            @search="onSearch"
          />
        </a-form-item>
    
        <a-form-item label="เลือกผู้ร่วมประชุมแแล้ว">

 <a-alert
           :message="this.message_alert"
            show-icon
            description="ท่านเลือกสามารถลบได้ โดยการกดกากบาท x"
          />

         <template v-for="(tag, index) in tags">
      <a-tooltip v-if="tag.length > 20" :key="tag" :title="tag">
        <a-tag :key="tag" :closable="index !== 0" @close="() => handleClose(tag)">
          {{ `${tag.slice(0, 20)}...` }}
        </a-tag>
      </a-tooltip>
      <a-tag v-else :key="tag" color="green" :closable="index !== 0" @close="() => handleClose(tag)">
        {{ tag }}
      </a-tag>
    </template>
     </a-form-item>
      </a-form>






      <template slot="footer">
        <a-button key="back" @click="CloseDialog()">
          ยกเลิก
        </a-button>
        <a-button
          key="submit"
          type="primary"
          :loading="loading"
          @click="API_SendAddData()"
        >
          ยืนยัน
        </a-button>
      </template>
    </a-modal>
  </div>
</template>
<script>
const formItemLayout = {
  labelCol: { span: 2 },
  wrapperCol: { span: 4 },
};
const formTailLayout = {
  labelCol: { span: 2 },
  wrapperCol: { span: 4, offset: 2 },
};
export default {
  components: {},
  data: () => ({
    dialog_addMeeting: false,
    data_date: "",
    formItemLayout,
    formTailLayout,
    username: "",
    room_Meeting: "",
    loading: false,
    room_value: null,
    tags: ['เลือก','Noot', 'Dew', 'toon','path'],
    array_data: ["100", "200", "300"],
  }),
  methods: {
    open(data) {
      // console.log(data);
      this.data_date = data.date_source_api;
      this.dialog_addMeeting = true;
    },
    CloseDialog(e) {
      console.log(e);
      this.dialog_addMeeting = false;
    },
    select_room(data) {
      console.log(data);
      //  if(data ){

      //  }
    },
    onSearch(value) {
      console.log(value);
    },
    API_SendAddData() {},
  },
};
</script>
